@import "./src/assets/themes/appdb/variables.scss";

@import "@/assets/variables";

.main {
  display: block;
  flex-grow: 1;
  max-width: 100%;
}

.table-wrapper {
  @include notebook {
    // overflow-x: auto;
    // white-space: nowrap;
    width: 100vw;
    margin-left: -8px;
  }
}

.table {
  width: 100%;
  text-align: left;

  &__head {
    font-weight: 500;
  }

  &__cell {
    padding: 6px;
    vertical-align: middle;
  }

  &__row {
    border-top: 1px solid #e5e5e5;
  }

  tbody &__row:last-child {
    border-bottom: 1px solid #e5e5e5;
  }

  &__date {
    display: block;
    color: $text-secondary;
    font-size: 12px;
    margin-top: 5px;
  }
}

.pagination {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;

  &__prev,
  &__next {
    color: var(--text-tertiary);
    font-size: 12px;
    display: flex;
    align-items: center;
  }

  &__icon {
    margin-left: 5px;
  }

  &__prev {
    .pagination__icon {
      transform: rotate(180deg);
      margin-right: 5px;
      margin-left: 0;
    }
  }
}

.row {
  display: flex;
  align-items: flex-start;

  @include notebook {
    flex-wrap: wrap;
  }
}

.col {
  max-width: 100%;

  &_1 {
    flex-grow: 1;

    @include notebook {
      order: 1;
    }
  }

  &_2 {
    flex: 0 0 192px;
    margin-left: 15px;

    @include notebook {
      margin-left: 0;
      order: 0;
      margin-bottom: 15px;
    }
  }

  @include notebook {
    flex: 0 0 100%;
  }
}

.sidebar {
  background: var(--secondary-bg);
  border-radius: 8px;
  padding: 10px;

  &__heading {
    font-weight: 500;
    margin-bottom: 10px;
  }
}

.desc {
  display: flex;
  align-items: flex-start;

  &__content {
    margin-left: 20px;

    @include notebook {
      margin-left: 0;
    }
  }

  &__content-text {
    &::v-deep {
      p {
        margin-bottom: 20px;
      }
    }
  }

  &__icon {
    @include notebook {
      display: none;
    }
  }
}

.steps {
  display: flex;

  &__link {
    display: flex;
    align-items: center;
    margin-right: 5px;
    background: var(--bg);
    border: 1px solid rgba(1, 108, 250, 0.5);
    border-radius: 14px;
    padding: 4px 8px 4px 4px;
    color: var(--text-primary);
  }

  &__num {
    color: #fff;
    width: 20px;
    height: 20px;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 4px;
    background: #525d66;
  }

  &__icon {
    margin-left: 4px;
  }
}

.h2 {
  font-size: 16px;
  font-weight: 600;
  margin-top: 30px;
  margin-bottom: 10px;
}
